<!-- by 添少 -->
<template>
  <section>
    <topbar>
      <template slot="before">
        <b class="iconfont icon-back" @click="$router.go(-1)"></b>
      </template>
      教练详情
    </topbar>
    <section class="preview">
      <div class="cover">
        <img class="db fit-cover" :src="detail.mainimg"/>
      </div>
      <div class="text">
        <h3>{{detail.name}}</h3>
        <p class="info">{{`${detail.age}岁 ${detail.city} ${detail.lang}`}}</p>
        <p>{{detail.desc}}</p>
      </div>
    </section>
    <section class="layout">
      <ul class="ui-group">
        <mt-cell title="创建的俱乐部" :value="clubCreateList[0]&&clubCreateList[0].name||'无'" :to="`/personalHomePage/myClub?coachId=${this.$route.query.coachId}`" is-link></mt-cell>
        <mt-cell title="加入的俱乐部" :value="clubJoinList[0]&&clubJoinList[0].name||'无'" :to="`/personalHomePage/myClub?coachId=${this.$route.query.coachId}`" is-link></mt-cell>
      </ul>
    </section>
  </section>
</template>

<script>
  import { mapState } from 'vuex'
  import Topbar from '@/components/topbar'

  export default {
    name: 'coach',
    components: {
      Topbar,
    },
    data () {
      return {
        detail: {},
        clubCreateList: [],
        clubJoinList: [],
      }
    },
    computed: {
      ...mapState({
      }),
    },
    created () {
      this.init()
    },
    methods: {
      init () {
        if (this.$route.query.coachId) {
          this.$http.get(this.$port.GET_COACH_DETAIL, {
            params: {
              coachId: this.$route.query.coachId
            }
          }).then(res => {
            if (res.code === 200) {
              this.detail = res.data
              this.clubCreateList = res.clubCreateList || []
              this.clubJoinList = res.clubJoinList || []
            }
          })
        }
      },
    }
  }
</script>

<style lang="scss" scoped>


  .ui-button {
    background-color: #fff;
    padding: 6px 10px;
    border-radius: 10px;
    border-width: 0;
    &.primary {
      background-color: #1797c6;
      color: #fff;
    }
    &.disabled {
      background-color: #ccc;
      color: #fff;
    }
  }

  .layout {
    background-color: #fff;
    margin-bottom: 10px;
  }

  .preview {
    background-color: #fff;
    color: #333;
    margin-bottom: 10px;
    .text {
      padding: 0 15px 10px;
      margin-bottom: 10px;
    }
    .info {
      font-size: 14px;
      color: #888;
    }

    h3 {
      font-size: 22px;
      font-weight: 600;
      padding-top: 10px;
    }
  }

</style>



